import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"
import Navbar from "react-bootstrap/Navbar"
import Nav from "react-bootstrap/Nav"
import Container from "react-bootstrap/Container"
import AriesFoodieLogo from "../images/aries-foodie.jpg"
import Img from "gatsby-image"

function NavbarBrand(props) {
  return (
    <Link className="navbar-brand" {...props}>{props.children}</Link>
  )
}

function NavLink(props) {
  return (
    <Link className="nav-link" {...props}>{props.children}</Link>
  )
}

const Header = ({ siteTitle, siteLogo, menuLinks }) => (
  <header>
    <Navbar collapseOnSelect expand="lg">
      <Container>
        <NavbarBrand to="/">
          <Img fixed={siteLogo} />
        </NavbarBrand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="ml-auto">
            {
              menuLinks.map(({ name, to }) => (<NavLink key={to} to={to}>{name}</NavLink>))
            }
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  </header>
);

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
